export const GridSpanSwitcher = (input_size: any) => {
    
    var spanLogic

    switch(input_size) {
        case 'xs':
            spanLogic = 2
            break
        case 'small':
            spanLogic = 3
            break
        case 'medium':
            spanLogic = 4
            break
        case 'large':
            spanLogic = 6
            break
        case 'xl':
            spanLogic = 12
            break
        default:
            spanLogic = 3
    }

    return spanLogic
}


export const GridStyle = (number: number) => {
    return {
        "gridTemplateColumns": `repeat(${number}, 1fr)`
    }
}